@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');



*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}


.mainSideBar {

    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 6vmin;
    background-color: #0D5DCD;
    z-index: 10;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    font-family: 'Righteous', cursive;




}

.logoSideBar {
    width: 15%;
    padding-left: 30px;
    align-items: center;
    justify-content: space-around;
    font-size: 2.4vmin;
    color: white;

}



.navItemsSideBar {
  

    width: 65%;

    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
    align-items: center;



    /* border: 1px solid palevioletred; */

}

.navItemsSideBar a {
    text-decoration: none;
    color: white;
    font-size: 2.12vmin;

}

.navItemsSideBar a:hover {
    text-decoration: none;
    transition: 750ms;
    transform: scale(.96);
    color: #FFA500;
}






.main768SideBar {
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    background-color: #0D5DCD;
    z-index: 10;
    color: white;
    display: none;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

}

.barSideBar {

    display: none;
}


@media only screen and (max-width:1200px) {

    .logoSideBar {
        width: 15%;
        padding-left: 30px;
        align-items: center;
        justify-content: space-around;
        font-size: 2.4vmin;
        color: white;

    }

    .navItemsSideBar {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        list-style: none;
        align-self: center;



        /* border: 1px solid palevioletred; */

    }

    .navItemsSideBar a {
        text-decoration: none;
        color: white;
        font-size: 2vmin;

    }


}


@media only screen and (max-width:768px) {

    .mainSideBar {

        display: none;


    }



    .main768SideBar {
        position: fixed;
        top: 0%;
        left: 0%;
        width: 100%;
        height: 120vmin;
        background-color: #0D5DCD;
        z-index: 10;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        transition: all 0.7s ease-out;
        cursor: pointer;

        font-family: 'Righteous', cursive;





    }

    .main768SideBar a {
        text-decoration: none;
        color: white;
        font-size: 2.2vmin;
    }


    .barSideBar {

        display: block;

        position: fixed;
        top: 2%;
        right: 4%;
        font-size: 3.5vmin;
        z-index: 50;
        transform: rotate(90deg);


    }



}



@media only screen and (max-width:600px) {

    .main768SideBar {
        position: fixed;
        top: 0%;
        left: 0%;
        width: 100%;
        height: 120vmin;
        transition: all 0.7s ease-out;
        background-color: #0D5DCD;
        z-index: 10;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        cursor: pointer;






    }


    .barSideBar {

        display: block;

        position: fixed;
        top: 3%;
        right: 5%;
        font-size: 4vmin;
        z-index: 50;


    }


    .main768SideBar a {
        text-decoration: none;
        color: white;
        font-size: 3vmin;
    }



}





@media only screen and (max-width:500px) {




    .barSideBar {

        display: block;

        position: fixed;
        top: 3%;
        right: 5%;
        font-size: 4vmin;
        z-index: 50;


    }




}



